import React, {useEffect, useState} from 'react'
import {Link } from 'react-router-dom'
import { Row, Col, Form, Button, ListGroup, Image, Card } from 'react-bootstrap'
import { LinkContainer} from 'react-router-bootstrap'
import {useSelector, useDispatch } from 'react-redux'
import RoundLoader from '../RoundLoader'
import { ToastContainer } from "react-toastify"
import "react-toastify/dist/ReactToastify.css"
import { toast } from "react-toastify"
import Loader from '../Loader'
import Message from '../Message'
import { getOrderDetails, payOrder, deliverOrder} from '../../actions/orderAction'
import axios from 'axios'
import {PayPalButton } from 'react-paypal-button-v2'
import { ORDER_PAY_RESET, ORDER_DELIVER_RESET} from '../../constants/constants'

const OrderScreen = ({match, history}) => {
     
    const  [sdkReady, setSdkReady] = useState(false)

    const orderId = match.params.id
    const dispatch  = useDispatch()
    const cart = useSelector(state => state.cart)

    const orderDetails = useSelector(state => state.orderDetails)
    const {order, loading, error } = orderDetails

    const orderDeliver = useSelector(state => state.orderDeliver)
    const { loading: loadingDeliver, success: successDeliver } = orderDeliver

    const orderPay = useSelector(state => state.orderPay)
    const { loading: loadingPay, success: successPay } = orderPay

    
    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin


    if(!loading){
      const addDecimals = (num) => {
        return (Math.round(num * 100) / 100).toFixed(2)
      }
      order.itemsPrice = addDecimals(
        order.orderItems.reduce((acc, item) => acc + item.price * item.qty, 0)
      )

    }

    useEffect(()=>{
        if(!userInfo){
            history.push('/login')
        }
        
        const addPayPalScript = async ()=>{
            const {data: clientId} = await axios.get('/api/config/paypal')
            const script = document.createElement('script')
            script.type = 'text/javascript'
            script.src = `https://www.paypal.com/sdk/js?client-id=${clientId}`
            script.async = true
            script.onload = ()=>{
                setSdkReady(true)
            }
            document.body.appendChild(script)
        }

        if( !order || successPay || successDeliver){
            dispatch({type: ORDER_PAY_RESET})
            dispatch({type: ORDER_DELIVER_RESET})
            dispatch(getOrderDetails(orderId))
        } else if (!order.isPaid){
         if(!window.paypal){
             addPayPalScript() 
         } else {
             setSdkReady(true)
         }

        }
    }, [dispatch, orderId, order,userInfo, successDeliver, successPay])

    const successPaymentHandler = (paymentResult) =>{
        console.log(paymentResult)
        dispatch(payOrder(orderId, paymentResult))

    }

    const deliverHandler = () =>{
        dispatch(deliverOrder(order))
    }
    

    return ( loading ? <Loader /> : error ? <ToastContainer position="bottom-left" autoClose={5000} 
    hideProgressBar={false} newestOnTop={false} 
    closeOnClick rtl={false} pauseOnFocusLoss draggable pauseOnHover /> : <>
    <h1>Order {(order._id).toUpperCase()} </h1>
     <Row>
        <Col md={8}>
            <ListGroup variant='flush'>
                <ListGroup.Item>
                    <h2>Shipping</h2>
                   <p> <strong>Name: </strong> { order.user.name } </p>
                   <p><strong>Email: </strong><a href={`mailto:${order.user.email}`} >{order.user.email}</a> </p> 
                    <p>
                        <strong>Address: </strong>
                        { order.shippingAddress.address}, {order.shippingAddress.city}, {order.shippingAddress.postalCode}, {' '}
                        {order.shippingAddress.country}
                    </p>
                    {order.isDelivered ? <Message variant='success'>Delivered on {order.deliveredAt}</Message> : <Message variant='danger'>
                  Not Delivered Yet
                  </Message>}

                </ListGroup.Item>
                <ListGroup.Item>
                    <h2>Payment Method</h2>
                    <p>
                    <strong>Method: </strong> {order.paymentMethod}
                    </p>
                {order.isPaid ? <Message variant='success'>Paid on {order.paidAt}</Message> : <Message variant='danger'>
                  Not Paid Yet
                  </Message>}
                </ListGroup.Item>

                <ListGroup.Item>
                    <h2>Order Items</h2>
                    {order.orderItems.length === 0 ? <Message variant='info'>Your Order is Empty</Message> : <ListGroup variant='flush'>
                       {order.orderItems.map((item, index)=> (
                           <ListGroup.Item key={index}>
                               <Row>
                                   <Col md={1}>
                                       <Image className='mb-2' src={item.image} alt={item.name} fluid rounded></Image>
                                   </Col>
                                   <Col>
                                   <Link  to={`/product/${item.product}`}>
                                       {item.name}
                                   </Link>
                                   </Col>
                                   <Col md={4}>
                                       {item.qty} x ${item.price} = ${(item.qty*item.price).toFixed(2)}
                                   </Col>
                               </Row>

                           </ListGroup.Item>
                       ))}
                        </ListGroup>}
                </ListGroup.Item>
            </ListGroup>
        </Col>

        <Col md={4}>
            <Card>
                <ListGroup variant='flush'>
                    <ListGroup.Item>
                        <h3 className='text-center'>Order Summery</h3>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Row>
                            <Col>
                            Items
                            </Col>
                            <Col><i className='fas fa-dollar-sign' style={{color: 'green'}}></i> {order.itemsPrice}</Col>
                        </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Row>
                            <Col>
                          Shipping
                            </Col>
                            <Col><i className='fas fa-dollar-sign' style={{color: 'green'}}></i> {order.shippingPrice}</Col>
                        </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Row>
                            <Col>
                            Tax
                            </Col>
                            <Col><i className='fas fa-dollar-sign' style={{color: 'green'}}></i> {Number(order.taxPrice).toFixed(2)}</Col>
                        </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Row>
                            <Col>
                            Total Cost
                            </Col>
                            <Col><i className='fas fa-dollar-sign' style={{color: 'green'}}></i> {Number(order.totalPrice).toFixed(2)}</Col>
                        </Row>
                    </ListGroup.Item>

                  { !order.isPaid && (
                      <ListGroup.Item>
                           { loadingPay && <Loader /> }
                           { !sdkReady ? <Loader /> : (
                               <PayPalButton amount={order.itemsPrice}
                               onSuccess = {successPaymentHandler}
                               >

                               </PayPalButton>
                           ) }      
                      </ListGroup.Item>
                  ) }

                  <ListGroup.Item>
                      {loadingDeliver && <RoundLoader />}
                      {userInfo && userInfo.isAdmin && order.isPaid && !order.isDelivered &&(
                               <Button type='button' className='btn btn-block' variant='info'
                               onClick={deliverHandler}>
                                   Mark As Deliver

                               </Button>
                           )}
                  </ListGroup.Item>
                  

                </ListGroup>
            </Card>
        </Col>
    </Row>
    </>
       
    )
}

export default OrderScreen
