import React, {useEffect, useState} from 'react'
import {Link } from 'react-router-dom'
import { Row, Col, Table, Form, Button, Card } from 'react-bootstrap'
import { LinkContainer} from 'react-router-bootstrap'
import {useSelector, useDispatch } from 'react-redux'
import Message from '../Message'
import RoundLoader from '../RoundLoader'
import { getUserDetails, updateUserProfile} from '../../actions/userAction'
import { ToastContainer } from "react-toastify"
import "react-toastify/dist/ReactToastify.css"
import { toast } from "react-toastify"
import Loader from '../Loader'
import { USER_UPDATE_PROFILE_RESET} from '../../constants/constants'
import { listMyOrders} from '../../actions/orderAction'

const ProfileScreen = ({ location, history }) => {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const [message, setMessage] = useState(null)
    const dispatch = useDispatch()
  
    const userDetails = useSelector((state) => state.userDetails)
    const { loading, error, user } = userDetails
  
    const userLogin = useSelector((state) => state.userLogin)
    const { userInfo } = userLogin
  
    const userUpdateProfile = useSelector((state) => state.userUpdateProfile)
    const { success } = userUpdateProfile

    const orderMyList = useSelector(state => state.orderMyList)
    const {loading: loadingOrders, success: successMyList, error: errorOrders, orders } = orderMyList

  
    useEffect(() => {
      if (!userInfo) {
        history.push('/login')
      } else {
        if (!user || !user.name) {
         dispatch({type: USER_UPDATE_PROFILE_RESET})
          dispatch(getUserDetails())
          dispatch(listMyOrders())
       
        } else {
         setName(user.name)
          setEmail(user.email)
        }
      }
    }, [dispatch, history, userInfo, user, successMyList, success,getUserDetails, listMyOrders])
  
    const submitHandler = (e) => {
      e.preventDefault()
      if (password !== confirmPassword) {
        setMessage(toast.error('Password Did not Match!', {
          position: "bottom-left",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          }))
      } else {
        dispatch(updateUserProfile({
          _id: user._id, name, email, password
        }))

      }
    }
    return (
      <Row>
        <Col md={3}>
          <h2>User Profile</h2>
        
          <ToastContainer position="bottom-left" autoClose={5000} 
            hideProgressBar={false} newestOnTop={false} 
            closeOnClick rtl={false} pauseOnFocusLoss draggable pauseOnHover /> 

            {loading && loadingOrders ? <Loader /> : error ? <Message>AN Error Occured</Message> : (
               <Form onSubmit={submitHandler}>
              <Form.Group controlId='name'>
                <Form.Label>Update Name</Form.Label>
                <Form.Control
                  type='name'
                  placeholder='Enter name'
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                ></Form.Control>
              </Form.Group>
  
              <Form.Group controlId='email'>
                <Form.Label>Update Email Address</Form.Label>
                <Form.Control
                  type='email'
                  placeholder='Enter email'
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                ></Form.Control>
              </Form.Group>
  
              <Form.Group controlId='password'>
                <Form.Label>Change Password</Form.Label>
                <Form.Control
                  type='password'
                  placeholder='Enter password'
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></Form.Control>
              </Form.Group>
  
              <Form.Group controlId='confirmPassword'>
                <Form.Label>Confirm Password</Form.Label>
                <Form.Control
                  type='password'
                  placeholder='Confirm password'
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                ></Form.Control>
              </Form.Group>
  
              <Button type='submit' variant='primary'>
                Update
              </Button>
            </Form>

            )}
           
          
        </Col>
        <Col md={9}>
          <h2> My Orders</h2>
          {loadingOrders ? <RoundLoader />: error ? <Message variant='danger'>An Error Occured</Message> : (
            <Table striped bordered responsive hover className='table-sm'>
              <thead>
                <tr>
                  <th>ID</th>
                  <th>DATE</th>
                  <th>TOTAL</th>
                  <th>PAID</th>
                  <th>DELIVERED</th>
                  <th></th>
                
                </tr>
              </thead>
              <tbody>
                {orders.map(order => (
                  <tr key={order._id}>
                    <td>{(order._id).toUpperCase()}</td>
                    <td>{order.createdAt.substring(0,10)}</td>
                    <td>$ {(order.totalPrice).toFixed(2)}</td>
                    <td>{order.isPaid? (order.paidAt.substring(0,10)) : (
                      <i className='fas fa-times' style={{color: 'red'}}></i>
                    )}</td>
                     <td>
                       {order.isDelivered ? (order.deliveredAt.substring(0,10)) : (
                      <i className='fas fa-times' style={{color: 'red'}}></i>
                    )}
                    </td>
                    <td>
                      <LinkContainer to={`/order/${order._id}`}>
                        <Button variant='light' className='btn-sm'>Details</Button>
                      </LinkContainer>
                    </td>
                    
                  </tr>
                ))}
              </tbody>

            </Table>
          ) }
        </Col>
     
      </Row>
    )
  }
  export default ProfileScreen  
